/*******************************
 * Class Diagrams
 *******************************/
.class-diagrams {
  text-align: center;
}

.class-diagram-spacer {
  background: #efefef;
  padding: 0;
}

.table-class-diagram tr.class-diagram-spacer td {
  padding: 0;
  border: 1px solid #efefef;
}

.table-class-diagram {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin-left: 20px;
  margin-bottom: 10px;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  font-family: Menlo, Monaco, Consolas, fixed;
}

.table-class-diagram th {
  background: #3981b2;
  color: #efefef;
  text-align: center;
  padding: 5px;
  padding-top: 5px !important;
}

.table-class-diagram tr td {
  padding: 5px;
  padding-top: 5px !important;
  border-right: 1px dashed #ccc;
}

.table-class-diagram tr {
  border-bottom: 1px solid #eee;
}

.table-class-diagram tr:last-child,
.table-class-diagram tr:first-child {
  border-bottom: none;
}

