/* styles for 341 from birch */
.evenlySpaced td {
  width: 50%;
}
.time {
  float: right;
  padding-right: 50px;
}
.time:after {
  content: ')';
}
.time:before {
  content: '(';
}

/* styles for 341 prove rubrics */
table.rubric th,
table.rubric td {
  border: 1px solid black;
  padding: 5px;
  vertical-align: middle;
  text-align: center;
  font-size: 90%;
  cursor: default;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
table.rubric thead tr:first-child > * {
  border-bottom: none;
}
table.rubric thead tr:last-child > * {
  border-top: none;
}
table.rubric thead {
  background-color: #326ba9;
  color: white;
}
table.rubric thead > :nth-child(2) {
  font-weight: bold;
}
table.rubric tbody td:first-child {
  text-align: left;
}
table.rubric tbody tr:nth-child(even) td {
  background-color: #00000011;
}
/* For grade calculator */
table.rubric tbody td:not(:first-child):not(:last-child) {
  cursor: pointer;
}
table.rubric tbody td.selected {
  background-color: #00000033 !important;
}
table.rubric tfoot input#points {
  width: 10%;
}

/* Image captions */
/*
    div.caption
        img
        span.tr|br|tl|bl Caption Text
 */
div.caption {
  position: relative;
  text-align: center;
  color: lightgray;
}
div.caption a,
div.caption a:hover {
  color: lightgray;
}
div.caption > span {
  position: absolute;
}
div.caption > span.tr {
  top: 4px;
  right: 20px;
}
div.caption > span.br {
  bottom: 4px;
  right: 20px;
}
div.caption > span.tl {
  top: 4px;
  left: 20px;
}
div.caption > span.bl {
  bottom: 4px;
  left: 20px;
}

